/*@import '_bootstrap.css';*/
@import '_foundation.css';
@import '_dat.gui.css';
@import '_fonts.css';

.jumbotron { background-color: transparent; }

body {
    font-family: 'GraphikLC-Regular', sans-serif;
    background-color: black;
    color: white;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#logo {
    color: white;
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
}

.logo-shrinkable {
    display: inline-block;
    vertical-align: top;
}

.logo-shrinkable {
    position: relative;
    overflow: hidden;
    display: inline-block;
}

.hq-slide {
    padding-top: 40px;
    padding-left: 32px;
    padding-right: 120px;
}

@media screen and (max-device-width: 1024px) {
    .hq-slide {
        padding-left: 40px;
        padding-right: 40px;
    }
}

.navbar {
    padding: 23px 25px;
}

@media screen and (max-device-width: 1024px) {
    .navbar {
        padding: 0;
    }
}

.navbar a {
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
}

@media screen and (max-device-width: 1024px) {
    ul.navbar-nav {
        background: white;
        z-index: 100;
        position: fixed;
        left: 0;
        right: 0;
        -ms-flex-direction: column !important;
            flex-direction: column !important;
        -ms-flex-pack: start !important;
            justify-content: flex-start !important;

        padding: 40px;
        min-height: 100vh;
    }

    ul.navbar-nav .nav-item {
        border-bottom: 1px solid #E5E5E5;
    }

    ul.navbar-nav.menu a {
        color: black;
        font-family: 'Sequel 100 Wide 95 Wide';
        font-size: 22px;
        text-transform: uppercase;
        padding: 1rem 0;
    }

    ul.navbar-nav.menu .active > a {
        color: #0d2797;
    }
}

.hq-navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
}

.body-dark a {
    color: white;
}

.body-light .navbar a {
    color: rgba(0, 0, 0, 0.185);
}

.body-light .navbar .active a {
    color: #133192;
}

.body-light .hq-slide,
.body-light .hq-slide a {
    color: black;
}

.body-light h1 {
    color: #e5e5e5;
}

.hq-slide h1 {
    line-height: 94px;
    font-size: 94px;
    margin-top: 3.55rem;
    margin-bottom: 6.9rem;

    position: relative;
}

.hq-slide h1:after {
    content: '.';
}

@media screen and (max-device-width: 1024px) {
    .hq-slide h1 {
        font-size: 36px;
        line-height: 73px;
        margin-top: 3rem;
        margin-bottom: 0.8rem;
        text-align: center;
    }
}

.scene-container .jumbotron,
.scene-container .container {
    position: relative;
    z-index: 2;
}

.body-index .pagination {
    display: none !important;
}

.body-main .jumbotron {
    margin-top: 15.1rem;
}

@media screen and (max-device-width: 1024px) {
    .body-main .jumbotron {
        margin-top: 4.1rem;
    }
}

.body-explore .pagination {
    display: none !important;;
}

.hq-navigation-explore {
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    font-size: 14px;
}

.body-mission #logo,
.body-bg #logo,
.body-products #logo,
.body-contacts #logo {
    color: #133192;
}

@media screen and (max-device-width: 1024px) {
    .body-mission #logo {
        color: white;
    }
}

.body-mission h1,
.body-science h1,
.body-team h1 {
    color: #2d2d2d;
}

@media screen and (max-device-width: 1024px) {
    .body-mission h1,
    .body-science h1,
    .body-team h1 {
        color: #2E2E2E;
    }
}

.body-bg h1,
.body-contacts h1 {
    color: #bd1e1b;
}

.body-contacts .hq-slide a {
    text-decoration: underline;
}

.body-context h1 {
    color: #04236c;
}

.body-science .jumbotron p {
    color: #ababab;
}

.team-member-photo {
    border-radius: 100%;
    overflow: hidden;
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    opacity: 0;
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}

.body-team .media-object-section:first-of-type {
    padding-top: 20px;
    padding-left: 0;
    width: 100%;
}

.body-team .media-object p {
    color: #7e7e7e;
    -webkit-transition: color 0.5s;
    -o-transition: color 0.5s;
    transition: color 0.5s;
}

.body-team .media-object:hover p {
    color: white;
}

.body-team .media-object:hover .team-member-photo {
    opacity: 1;
}

.body-team h3 {
    /* keep mobile-size fonts on desktop in the team slide (copied from foundation) */
    font-size: 1.1875rem;
    line-height: 1.4;
    margin-top: 0;
    margin-bottom: 0.5rem;
    /* make them bolder */
    font-weight: bolder;
}

@media screen and (max-device-width: 1024px) {
    .body-team h3 {
        font-size: 0.9875rem;
    }
}

.body-team .media-object .lead {
    color: white;
    font-size: inherit;
    margin-bottom: 0.2em;
}

@media screen and (max-device-width: 1024px) {
    .body-team .media-object .lead,
    .body-team .media-object p {
        font-size: 0.7875rem;
    }
}

.body-index {
    background-color: #042a85
}

.body-explore {
    background-color: #042a85
}

.body-main {
    background-color: #042a85
}

.body-mission {
    background-color: #1e1e1e
}

.body-bg {
    background-color: #fc2622
}

.body-how {
    background-color: #042a85
}

.body-context {
    background-color: #042a85
}

.body-science {
    background-color: #1e1e1e
}

.body-products {
    background-color: #ffffff
}

.body-team {
    background-color: #1e1e1e
}

.body-contacts {
    background-color: #fc2622
}

body {
    -webkit-transition: background-color 0.7s;
    -o-transition: background-color 0.7s;
    transition: background-color 0.7s;
}

#logo {
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}

.body-index #logo {
    opacity: 0;
}

.logo-shrinkable {
    width: 1em;
    -webkit-transition: width 0.5s;
    -o-transition: width 0.5s;
    transition: width 0.5s;
}

.body-index .logo-shrinkable {
    width: 7em;
}

.body-explore .logo-shrinkable {
    width: 7em;
}

.body-main .logo-shrinkable {
    width: 7em;
}

#navbarSupportedContent {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: transform 0.5s;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    -o-transition: transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.body-index #navbarSupportedContent {
    -webkit-transform: translateX(750px);
        -ms-transform: translateX(750px);
            transform: translateX(750px);
}

.body-explore #navbarSupportedContent {
    -webkit-transform: translateX(750px);
        -ms-transform: translateX(750px);
            transform: translateX(750px);
}

.body-main #navbarSupportedContent {
    -webkit-transform: translateX(750px);
        -ms-transform: translateX(750px);
            transform: translateX(750px);
}

@media screen and (max-device-width: 1024px) {
    #navbarSupportedContent {
        -webkit-transform: translateX(750px);
            -ms-transform: translateX(750px);
                transform: translateX(750px);
    }

    #navbarSupportedContent.visible {
        -webkit-transform: translateX(0) !important;
            -ms-transform: translateX(0) !important;
                transform: translateX(0) !important;
    }
}

.separator-40 {
    width: 40px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin: 40px 0;
    display: none;
}

@media screen and (max-device-width: 1024px) {
    /* >= 640px? */
    .separator-40 {
        border-top: 1px solid rgba(255, 255, 255, 0.12);
        margin: 30px 0;
        display: block;
    }
}

@media print, screen and (max-width: 639px) {
    /* 40em -1px */
    h2 {
        font-size: 23px;
    }
}

.body-light .separator-40 {
    border-top-color: #e5e5e5;
}

.body-how .jumbotron {
    padding-top: 140px;
}

@media screen and (max-device-width: 1024px) {
    .body-how .jumbotron {
        padding-top: 65px;
    }
}

.hq-slide {
    visibility: hidden;
}

.hq-slide.active-slide {
    visibility: visible;
}

.body-animation .hq-slide {
    visibility: hidden !important;
}

.hq-slide .animation-1,
.hq-slide .animation-2,
.hq-slide .animation-3,
.hq-slide .animation-4 {
    opacity: 0;
    margin-top: 50px;
}

.hq-slide.active-slide .animation-1 {
    opacity: 1;
    margin-top: 0;
    -webkit-transition: opacity 2s, margin-top 1s;
    -o-transition: opacity 2s, margin-top 1s;
    transition: opacity 2s, margin-top 1s;
}

.hq-slide.active-slide .animation-2 {
    opacity: 1;
    margin-top: 0;
    -webkit-transition: opacity 2s 0.4s, margin-top 1s 0.4s;
    -o-transition: opacity 2s 0.4s, margin-top 1s 0.4s;
    transition: opacity 2s 0.4s, margin-top 1s 0.4s;
}

.hq-slide.active-slide .animation-3 {
    opacity: 1;
    margin-top: 0;
    -webkit-transition: opacity 2s 0.7s, margin-top 1s 0.7s;
    -o-transition: opacity 2s 0.7s, margin-top 1s 0.7s;
    transition: opacity 2s 0.7s, margin-top 1s 0.7s;
}

.hq-slide.active-slide .animation-4 {
    opacity: 1;
    margin-top: 0;
    -webkit-transition: opacity 2s 1.0s, margin-top 1s 1.0s;
    -o-transition: opacity 2s 1.0s, margin-top 1s 1.0s;
    transition: opacity 2s 1.0s, margin-top 1s 1.0s;
}

.body-products .list-group-item {
    cursor: pointer;
    font-size: 28px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 55px;
    -webkit-transition: margin-left 0.3s;
    -o-transition: margin-left 0.3s;
    transition: margin-left 0.3s;
}

.body-products .list-group-item.active {
    color: #0d2797;
}

.body-products .list-group-item:hover {
    margin-left: 15px;
}

@media screen and (max-device-width: 1024px) {
    .body-products .list-group-item {
        font-weight: normal;
        font-size: 16px;
        position: relative;
    }

    .body-products .list-group-item.active:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 24px;
        border-bottom: 1px solid #E5E5E5;
    }

    .body-products .list-group-item:hover {
        margin-left: 0;
    }

    .body-products .hq-slide.active-slide .animation-4 {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
    }

    .body-products .hq-slide.active-slide .animation-4 img {
        width: 48px;
        height: 48px;
    }
}

.body-products .item-descriptions {
    position: relative;
}

.body-products .item-description h3 {
    color: #0d2797;
    font-family: 'Sequel 100 Wide 95 Wide';
    font-size: 30px;
    text-transform: uppercase;
}

.body-products .item-description {
    position: absolute;
    opacity: 0;
    padding-top: 50px;
    -webkit-transition: opacity 0.3s, padding-top 0s 0.3s;
    -o-transition: opacity 0.3s, padding-top 0s 0.3s;
    transition: opacity 0.3s, padding-top 0s 0.3s;
}

.body-products .item-description.active {
    opacity: 1;
    padding-top: 0;
    -webkit-transition: opacity 0.6s 0.3s, padding-top 0.6s 0.3s;
    -o-transition: opacity 0.6s 0.3s, padding-top 0.6s 0.3s;
    transition: opacity 0.6s 0.3s, padding-top 0.6s 0.3s;
}

@media screen and (max-device-width: 1024px) {
    .body-products .item-description {
        margin-top: 80px;
    }
}

/* submenu in team slide (only? for now ?) */
.submenu {
    white-space: nowrap;
}

.submenu-item {
    display: inline-block;
    cursor: pointer;
    font-family: 'GraphikLC-Regular', sans-serif;
    font-size: 16px;
    padding-bottom: 7px;
    margin-right: 20px;
    color: #7e7e7e;
    -webkit-transition: color 0.7s;
    -o-transition: color 0.7s;
    transition: color 0.7s;
}

.submenu-item:hover,
.submenu-item.selected {
    border-bottom: 1px solid white;
    color: white;
}

@media print, screen and (min-width: 64em) {
    /* used large-x columns in body-team screen to prevent overlapping issues */
    .body-team .submenu {
        display: none;
    }

    .body-team .inactive {
        display: -ms-flexbox;
        display: flex;
    }
}

@media print, screen and (max-width: 1023px) {
    /* 64em -1px; */
    .body-team .submenu {
        display: block;
    }

    .body-team .separator-40 {
        display: none;
    }

    .body-team .container {
        position: relative;
    }

    .body-team .row.active,
    .body-team .row.inactive {
        position: absolute;
    }

    .body-team .grid-margin-x > .cell {
        margin-left: 0;
    }

    .body-team .row.active {
        opacity: 1;
        margin-left: 0px;
        -webkit-transition: opacity 1s, margin-left 1s;
        -o-transition: opacity 1s, margin-left 1s;
        transition: opacity 1s, margin-left 1s;
    }

    .body-team .row.inactive {
        opacity: 0;
        margin-left: 20px;
        pointer-events: none;
    }

    .body-team .row.active .media-object-section {
        padding-left: 0;
    }

    .body-team h1 {
        margin-bottom: 1.5rem;
    }

    .body-team p {
        margin-bottom: 0;
    }

    .body-team .key-people .media-object-section:first-of-type {
        padding-top: 10px;
    }

    .body-team .key-people h3,
    .body-team .key-people p {
        font-size: 12px;
        font-weight: normal;
        letter-spacing: 0;
        line-height: 17px;
    }

    .body-team .media-object .team-member-photo {
        opacity: 1;
    }

    .body-contacts .separator-first {
        border-color: transparent;
        margin: 15px 0;
    }
}

@media screen and (max-device-width: 1024px) {
    .pagination {
        display: none !important;
    }
}

.lead .first-sentence {
    display: block;
}

.map-container {
    margin-bottom: 3rem;
}


